import { Component, OnInit } from "@angular/core";
import { UsersService } from "../services/users.service";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";
import { User } from "app/shared/models/user.model";

@Component({
  selector: "app-add-user",
  templateUrl: "./add-user.component.html",
  styleUrls: ["./add-user.component.scss"]
})
export class AddUserComponent implements OnInit {
  newuser: User;
  submitting: boolean;

  constructor(
    public usersservice: UsersService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newuser = new User({});
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newuser);

    swal
      .fire({
        title: "Do you want to add the user ?",
        text: "New user will be added in User List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.usersservice.addUser(this.newuser).subscribe(
            (data: any) => {
              swal.fire("Add!", "User has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
