import { Component, OnInit } from "@angular/core";

import { UsersService } from "../services/users.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { User } from "app/shared/models/user.model";

@Component({
  selector: "app-edit-user",
  templateUrl: "./edit-user.component.html",
  styleUrls: ["./edit-user.component.scss"]
})
export class EditUserComponent implements OnInit {
  updatedUser: User;
  userid;

  constructor(
    public usersservice: UsersService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedUser = new User({});

    this.userid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.usersservice.getUsersById(this.userid).subscribe(
      (data: any) => {
        console.log("userslist>>", data);
        this.updatedUser = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedUser);

    swal
      .fire({
        title: "Do you want to edit the user details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.usersservice.editUser(this.updatedUser).subscribe(
            (data: any) => {
              swal.fire("Edit!", "User has been edited.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
